import React, { useState, useEffect } from 'react';
import { Modal } from 'react-bootstrap';
import { isBrowser, isMobile, browserName, browserVersion, osName, osVersion, isChrome } from "react-device-detect";

function You(props) {
    const [showAboutYou, setShowAboutYou] = useState();
    const [data, setData] = useState({});
    const [battery, setBattery] = useState();
    const [ip, setIp] = useState();
    
    const height = window.innerHeight !== undefined ? window.innerHeight : false;
    const width = window.innerWidth !== undefined ? window.innerWidth : false;
    const mode = window.screen.orientation.type.indexOf('landscape') > -1 ? 'landscape' : 'portrait';
    const referrer = window.document.referrer !== undefined ? window.document.referrer : false;
    const memory = window.navigator.deviceMemory !== undefined ? window.navigator.deviceMemory : false;
    const speed = window.navigator.connection !== undefined ? window.navigator.connection.downlink : false;
    const connection = window.navigator.connection !== undefined ? window.navigator.connection.effectiveType : false;

    function fetchIP(){
        fetch('https://ipapi.co/json').then(res => res.json()).then(data => setIp(data))
    }
    
    useEffect(() => {
        if(isChrome){
          window.navigator.getBattery().then(function(b){
              setBattery(b.level)
          })
        }
        setData({'height': height, 'width': width, 'mode': mode, 'referrer': referrer, 'memory': memory, 'speed': speed, 'connection': connection})
        fetchIP();    
    }, [])
    function showAboutYouHandler(){
        setShowAboutYou(true)
    }
    
    function closeAboutYouHandler(){
        setShowAboutYou(false)
    }
    
    return (
        <div style={{margin: 'auto'}}>
        <button className="about_button" onClick={showAboutYouHandler}>
        <ion-icon name="person-add-outline"></ion-icon>            
        <div style={{display: 'inline', margin: '5px'}}>About You</div>
        </button>
        {showAboutYou && 
        <div className="overlay">
          <Modal.Dialog size="lg" aria-labelledby="contained-modal-title-vcenter" centered>
            <Modal.Header closeButton onClick={closeAboutYouHandler}>
              <Modal.Title>Lets C what I know about YOU</Modal.Title>
            </Modal.Header>
            <Modal.Body>
            <div style={{overflowY: 'scroll', height: '50vh'}}>
              <p>Hi Explorer, You appear to be visiting my website from <span style={{color: 'blue'}}>ip address {ip.ip}</span> whose internet service provider is <span style={{color: 'blue'}}>{ip.org}</span></p>
              { data.speed && data.connection && <p>Your network speed seems to be around <i style={{color: 'blue'}}>{data.speed}mbps</i> which is effectively a <i style={{color: 'blue'}}>{data.connection}</i> connection</p>}
              <p>I can see that you are using {isBrowser && <span style={{color: 'blue'}}>desktop</span>}{isMobile && <span style={{color: 'blue'}}>mobile</span>} specifically { browserName && <span style={{color: 'blue'}}>{browserName}</span>} browser of version {browserVersion && <span style={{color: 'blue'}}>{browserVersion}</span>}</p>
              <p>Your OS seem to be {osName && <span style={{color: 'blue'}}>{osName}</span>} of version {osVersion && <span style={{color: 'blue'}}>{osVersion}</span>} {data.memory && <span style={{color: 'blue'}}> having {data.memory}GB of RAM </span>}</p>
              <p>You seem to be at <span style={{color: 'blue'}}>{ip.latitude}</span> latitude and <span style={{color: 'blue'}}>{ip.longitude}</span> longitude in <span style={{color: 'blue'}}>{ip.city} {ip.region}</span> of country <span style={{color: 'blue'}}>{ip.country_name}</span> whose capital is <span style={{color: 'blue'}}>{ip.country_capital}</span> and currency is <span style={{color: 'blue'}}>{ip.currency} {ip.currency_name}</span></p>
              {data.height && data.width && <p>Your device's screen height is <i style={{color: 'blue'}}>{data.height}</i> pixels and screen width is <i style={{color: 'blue'}}>{data.width}</i> pixels, Your device is currently in the {data.mode && <i style={{color: 'blue'}}>{data.mode}</i>} mode and seems to be tilted at 0 degree angle</p>}
              {battery && <p>Your battery seems to be having <i style={{color: 'blue'}}>{battery * 100}%</i> charge.</p>}
              { data.referrer && <p>You seem to have arrived here from the website <i style={{color: 'blue'}}>{data.referrer}</i></p> }
            </div>
            </Modal.Body>
          </Modal.Dialog>
        </div>  
       }
    </div> 
    );
}

export default You;